.reviews {
	&__content {
		padding: 7.07rem 0 5.29rem 0;
		background-color: $whiteGray;
	}

	& .container {
		max-width: 1200px;
		box-sizing: border-box;
		padding: 0 30px;
	}

	&__slider {
		margin-top: 70px;

		&-slide {
			max-width: 300px;
			min-height: 210px;
		}
	}
}

.type-list {
	padding: 0;
	margin: 0;
	display: flex;
	justify-content: space-between;
	align-items: center;
	list-style-type: none;
	margin-top: 93px;

	&__item {
		&:not(:first-child) {
			margin-left: 35px;
		}
	}
}


/* media queries */
@media (max-width: $xl) {
	.reviews {
		& .swiper-container {
			width: 74%;
		}

		&__slider {
			&-slide {
				max-width: 230px;
				min-height: 180px;
			}
		}
	}

}

@media (max-width: $lg) {
	.reviews {
		& .swiper-container {
			width: 60%;
		}

		&__slider {
			&-slide {
				min-height: 140px;
			}
		}
	}

}

@media (max-width: $md) {
	.reviews {
		& .swiper-container {
			width: 68%;
		}

		& .slider {
			& .slider__slide {
				width: 200px; 

				& .slider__slide-cover {
					width: 100%;
					min-height: 140px;

					& img {
						width: 100%;
						height: 140px;
					}

					& a {
						display: none;
					}
				}
			}
		}
	}

	.type-list {
		flex-wrap: wrap;

		&__item {
			flex: 0 0 33.333%;
			text-align: center;
			&:not(:first-child) {
				margin-top: 10px;
				margin-left: 0;
			}

			& a {
				display: inline-block;
				padding: 12px 10px;
				font-size: 12px;
			}
		}
	}
}



@media (max-width: 680px) {
	.reviews {
		& .swiper-container {
			width: 80%;
		}
	}
}

@media (max-width: $sm) {
	.reviews {
		& .swiper-slide {
			width: 180px;
			height: 140px;
		}
	}

	.type-list {
		flex-wrap: wrap;

		&__item {
			flex: 0 0 50%;
			text-align: center;
		}
	}
}


@media (max-width: 480px) {
	.reviews {
		& .swiper-container {
			width: 100%;
		}
	}
}

@media (max-width: 460px) {
	.reviews {
		& .swiper-container {
			width: 70%;
		}

		& .swiper-slide {
			width: 300px;
			height: 180px;

			.slider__slide-cover {
				height: 180px;

				& img {
					min-height: 180px;					
				}
			}
		}
	}
}

@media (max-width: 460px) {
	.reviews {
		& .swiper-container {
			width: 80%;
		}
	}
}

