.about {
	position: relative;

	&__content {
		padding: 7.14rem 0 7.1rem 0;
	}

	&__inner {
		max-width: 850px;
	}

	&__title {
		margin: 0 auto;
	}

	&__description {
		max-width: 842px;
		margin: 79px auto 0 auto;
	}

	&__paragraph {
		line-height: 20px;
		color: #fff;
		text-align: center;

		&:not(:first-child) {
			margin-top: 30px;
		}
	}

	&__items {
		margin-top: 85px;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	&__item {
		color: #fff;
		max-width: 225px;
		text-align: center;

		&-title {
			@include ProximaBold;
			font-size: 48px;
			line-height: 20px;
			letter-spacing: 0.01em;

			& span { 
				font-size: 24px;
			}
		}

		&-text {
			color: #fff;
			margin-top: 29px;
			overflow: hidden;
		}
	}
}

@media (max-width: $xl) {
	.about {
		&__items {
			margin-top: 70px;
		}

		&__item {
			&-title {
				font-size: 44px;
			}
		}

		&__description {
			max-width: 700px;
			margin: 60px auto 0 auto;
		}
	}
}

@media (max-width: $lg) {
	.about {
		&__items {
			flex-wrap: wrap;
			justify-content: center;
			margin-top: 55px;
		}

		&__item {
			max-width: 300px;
			margin-top: 25px;

			&-title {
				font-size: 38px;
			}

			&-text {
				margin-top: 15px;
			}
		}

		&__description {
			max-width: 600px;
			margin: 50px auto 0 auto;
		}
	}
}

@media (max-width: $md) {
	.about {
		&__items {
			margin-top: 45px;
		}

		&__item {
			max-width: 220px;
			margin-top: 15px;

			&-title {
				font-size: 32px;
			}
		}

		&__description {
			max-width: 500px;
		}

		&__paragraph {
			&:not(:first-child) {
				margin-top: 25px;
			}
		}
	}
}

@media (max-width: $sm) {
	.about {
		&__item {
			max-width: 220px;
			margin-top: 15px;

			&-title {
				font-size: 28px;
			}
		}

		&__description {
			max-width: 375px;
			margin: 40px auto 0 auto;
		}

		&__paragraph {
			&:not(:first-child) {
				margin-top: 20px;
			}
		}
	}
}

@media (max-width: 425px) {
	.about {
		&__items {
			margin-top: 35px;
		}

		&__item {
			max-width: 220px;
			margin-top: 8px;

			&-title {
				font-size: 26px;
			}

			&-text {
				margin-top: 5px;
			}
		}

		&__description {
			max-width: 320px;
			margin: 40px auto 0 auto;
		}

		&__paragraph {
			&:not(:first-child) {
				margin-top: 20px;
			}
		}
	}
}

@media (max-width: 375px) {
	.about {
		&__description {
			max-width: 280px;
			margin: 25px auto 0 auto;
		}
	}
}