@import 'vars';
@import '_mixins';


*, *:before, *:after {
	box-sizing: border-box;
} 

 html  {
   width: 100%;
   height: 100%;
   margin: 0px;
   padding: 0px;
   overflow-x: hidden; 

}

html, body {
	@include ProximaRegular;
	font-size: 14px;
}

.container-fluid {
	padding: 0;
}

.container {
	max-width: 940px;
	// box-sizing: content-box;
}

.section-title {
	font-size: 30px;
	line-height: 22px;
	@include ProximaBold;
	color: $dark;
	margin-bottom: 0;
}

.section-title-white {
	font-size: 30px;
	line-height: 1.2;
	@include ProximaBold;
	color: #fff;
}

.desc-text {
	font-size: 14px;
	line-height: 22px;
	letter-spacing: 0.01em;
	@include ProximaLight;
	color: $dark;
	margin-bottom: 0;
}

.description-text {
	font-size: 16px;
	line-height: 22px;
	@include ProximaLight;
}

.strong-text {
	line-height: 22px;
	@include ProximaBold;
	color: $dark;
	letter-spacing: 0.01em;
}

.item-desc {
	font-size: 13px;
	line-height: 45px;
	@include ProximaLight;
	color: #000;
}

.btn-text {
	font-size: 18px;
	line-height: 40px;
	@include MullerBold;
	color: #000;
}

.red-logo {
	padding: 0;
	&-text {
		margin-left: 10px;
	}
}

/* item-link */
.item-link {
	@include MullerMedium;
	line-height: 1.2;
	font-size: 14px;
	text-transform: uppercase;
	letter-spacing: 0.1em;
	color: $lightGray;
	text-decoration: none;
	transition-property: color;
	transition-duration: 0.8s;

	&:hover {
		color: $red;
		text-decoration: none;
	}
}

.card-text {
	line-height: 40px;
	color: #000;
}

.active-item a {
	padding: 12px 10px;
	border: 2px solid $red;
	border-radius: 5px;
	color: $red;
}

.mini-desc {
	margin-bottom: 0;
	font-family: 'Muller', 'Proxima Nova', sans-serif;
	font-size: 17px;
	font-weight: 400;
	line-height: 22px;
	color: #000;
}

.app-link {
	width: 40px;
	height: 40px;
	border: 1px solid $red;
	border-radius: 5px;
}

.bg {
	position: absolute;
	z-index: -999;
	min-height: 100%;
}

.orange-bg {
	background-image: linear-gradient(0.0deg, #ff2e00 0%, #ff4c25 100%);
}

.green-bg {
	background-image: linear-gradient(0.0deg, #008e00 0%, #00a300 100%);
}

.blue-bg {
	background-image: linear-gradient(0.0deg, #001c67 0%, #02388f 100%);
}

.gray-bg {
	background-color: #9a9a9a;
}

.form-field {
	background-color: #ffffff;
	border: 1px solid #dddddd;
	border-radius: 5px;
	padding: 5px 12px;

	/* text */
	@include MullerLight;
	font-size: 16px;
	color: #000;
	outline: none;
}

.main-link {
	@include ProximaBold;
	font-size: 22px;
	color: #fff;
	text-decoration: none;
	transition-duration: 0.8s;

	&:hover {
		transform: rotate(720deg);
		color: #34eb92;
		letter-spacing: 2px;
		text-decoration: none;
	}
}

/*	Media queries */
@media (max-width: 1440px) {
	.bg {
		object-fit: cover;
	}
}

@media (max-width: $xl) {
	html, body {
		font-size: 13px;
	}

	.section-title {
		font-size: 28px;
	}

	.section-title-white {
		font-size: 28px;
	}
}

/* 992 */
@media (max-width: $lg) {
	html, body {
		font-size: 10px;
	}

	.section-title {
		font-size: 26px;
	}

	.section-title-white {
		font-size: 26px;
	}

	.description-text {
		font-size: 14px;
	}

	.mini-desc {
		font-size: 16px;
	}
}

/* 768 */
@media (max-width: $md) {
	html, body {
		font-size: 8px;
	}

	.section-title {
		font-size: 24px;
		text-align: center;
	}

	.section-title-white {
		font-size: 24px;
	}
}


/* 576 */
@media (max-width: $sm) {
	html, body {
		font-size: 6px;
	}

	.section-title {
		font-size: 22px;
		text-align: center;
	}

	.section-title-white {
		font-size: 22px;
	}

	.description-text {
		font-size: 14px;
	}

	.mini-desc {
		font-size: 15px;
	}

	.active-item a {
		border: none;
	}
}

@media (max-width: 425px) {
	html, body {
		font-size: 5px;
	}

	.section-title {
		font-size: 20px;
		text-align: center;
	}

	.section-title-white {
		font-size: 20px;
	}

	.description-text {
		line-height: 20px;
	}

	.mini-desc {
		font-size: 14px;
	}
}

@media (max-width: 375px) {
	html, body {
		font-size: 4px;
	}

	.mini-desc {
		font-size: 13px;
	}

	.item-link {
		font-size: 13px;
	}

	.btn-text {
		font-size: 16px;
	}
}

.open-modal {
	display: block;
	animation-name: slippery;
	animation-duration: 0.9s;
}

@keyframes slippery {
	0% {
		opacity: 0;
	}
	50% {
		opacity: 0.4;
		transform: translateY(-130%);
	}
	100% {
		opacity: 1;
		transform: translateY(0px);
	}
}


@import '_header';
@import '_subscription';
@import '_services';
@import '_about';
@import '_service-steps';
@import '_advantages';
@import '_free-consultation';
@import '_reviews';
@import '_partners';
@import '_rate';
@import '_contacts';
@import '_footer';
@import '_modal';
@import '_slider';