.rate {
	&__content {
		padding: 7.07rem 0 13.93rem 0;
		background-color: $whiteGray;
	}

	&__description {
		margin-top: 39px;

		&-paragraph {
			@include MullerBold;
			line-height: 25px;
			font-size: 18px;
			color: $lightGray;
		}
	}

	&__row {
		margin-top: 50px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		

		&-text {
			@include MullerLight;
			line-height: 1.2;
			font-size: 16px;
			color: $lightGray;
			margin-bottom: 0;
		}

		&-list {
			list-style-type: none;
			margin-bottom: 0;
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
	}

}

.cards {
	margin-top: 39px;
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
}

.card {
	padding: 52px 20px 50px 20px;
	width: 310px;
	height: 920px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;

	&__text {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	&__title {
		font-family: 'Muller', 'Proxima Nova', sans-serif;
		font-size: 20px;
		font-weight: 300;
		color: #000;
		text-align: center;

		& span {
			font-weight: 700;
		}
	}

	&__image {
		margin-top: 29px;
		max-width: 200px;
		max-height: 126px;

		& img {
			max-width: 100%;
			max-height: 100%;
		}
	}

	&__description {
		margin-top: 30px;
	}

	&__list {
		width: 256px;
		list-style-type: none;
		padding: 0;
		margin-bottom: 0;

		&-item {
			max-height: 38px;

			/* text */
			font-family: 'Muller';
			font-size: 11px;

			&:before {
				display: inline-block;
				margin-bottom: -19px;
				content: '';
				width: 280px;
				height: 1px;
				background-color: $whiteGray;
			}

			&-slogan {
				font-weight: 300;
			}

			&-desc {
				font-weight: 500;
			}
		}
	}

	&__feature {
		text-align: center;


		& small {
			@include MullerLight;
			font-size: 13px;
		}
	}

	&__price {
		@include MullerBold;
		font-size: 24px;
	}

	&__buttons {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-top: 33px;
	}

	&__btn {
		min-width: 200px;
		min-height: 55px;
		border-radius: 5px;
		padding-top: 7px;
		text-align: center;

		/* text */
		@include MullerBold;
		font-size: 18px;
		line-height: 40px;
		color: #fff;
		text-decoration: none;
		transition-duration: 0.9s;

		&:hover {
			color: #fff;
			text-decoration: none;
			background-color: $red;
		}

		&:not(:first-child) {
			margin-top: 15px;
		}
	}
}


/*	 media queries	*/
@media (max-width: $lg) {
	.rate {
		&__content {
			padding: 7rem 0 5rem 0;
		}

		&__row {
			justify-content: flex-start;
		}
	}

	.cards {
		justify-content: flex-start;
		flex-wrap: wrap;
	}

	.card {
		height: 820px;
		box-sizing: content-box;
		margin: 20px auto;
		padding: 30px 20px;

		&__title {
			font-size: 18px;
		}

		&__description {
			margin-top: 20px;
		}

		&__list {
			width: 230px;

			&-item {
				&:before {
					width: 220px;
				}
			}
		}
	}
}

@media (max-width: $md) {
	.rate {
		&__row {
			flex-wrap: wrap;

			&-list {
				margin: 40px auto 0 auto;
				background-color: $whiteGray;
				justify-content: space-around;
			}
		}

		&__description {
			&-paragraph {
				font-size: 15px;
			}
		}
	}

	.card {
		&__btn {
			transition-duration: 0s;
		}
	}
}


@media (max-width: $sm) {
	.rate {
		&__row {
			&-text {
				margin-left: 20px;
			}
		}

		&__description {
			margin-left: 20px;
			&-paragraph {
				font-size: 14px;
				line-height: 20px;

				&:not(:first-child) {
					margin-top: 14px; 
				}
			}
		}
	}

	.cards {
		justify-content: center;
		flex-wrap: wrap;
	}
}

@media (max-width: $sm) {
	.rate {
		&__row {
			&-text {
				font-size: 15px;
			}
		}
	}
}