.footer {
	&__content {
		padding: 48px 0 47px 0;
		background-color: #0e0e0e;

		& > .container > .row {
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: 0;
			max-width: 100%;
		}
	} 

	&__description {
		& span {
			@include MullerLight;
			font-size: 13px;
			line-height: 22px;
			color: #fff;
			margin-right: 24px;
		}
	}
}

/*	 media queries	*/
@media (max-width: $lg) {
	.footer {
		&__content {
			& > .container > .row {
				justify-content: space-around;
			}
		}
	}
}

@media (max-width: 425px) {
	.footer {
		&__content {
			& > .container > .row {
				flex-direction: column;
				align-items: center;
			}
		}

		&__description {
			margin-top: 15px;
		}
	}
}