.service-steps {
	background-color: #f5f5f5;
	
	&__content {
		padding: 7.14rem 0 7rem 0;
	}

	&__inner {
		max-width: 840px;
	}

	&__items {
		margin-top: 6.86rem;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	&__item {
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 0;

		&-paragraph {
			margin-top: 54px;
			text-align: center;
		}
	}
}

/*	 media queries	*/
@media (max-width: $md) {
	.service-steps {
		&__inner {
			max-width: 500px;
		}

		&__items {
			flex-wrap: wrap;
			margin-top: 3rem;
		}

		&__item {
			margin: 30px auto 0 auto; 

			&-paragraph {
				margin-top: 30px;
			}
		}
	}
}


