.modal {
	background-color: rgba(0, 0, 0, 0.5);
	
	&-dialog {
		margin: 5rem auto;
	}

	&-title {
		max-width: 320px;
	}
	
	&-header {
		border-bottom: none;
		padding: 30px 30px 0 75px;

		& .close {
			padding: 0;
			margin: 0;
			font-size: 36px;
			line-height: 25px;
		}
	}

	&-body {
		padding: 31px 75px 29px 75px;

		& .form {
			padding: 0;

			.form__fields {
				margin-top: 0;

				& label {
					&:first-child {
						margin-bottom: 11px;
					}
				}
			}
		}
	}

	&-description {
		max-width: 328px;
		padding: 0;
		margin-top: 28px;
	}

	&-paragraph {
		@include MullerLight;
		font-size: 16px;
		line-height: 20px;
		color: #000;
	}
}

.video-modal {
	// display: block;

	.modal-header {
		padding: 30px 30px 0 30px;
	}
	
	.modal-body {
		padding: 0 30px 26px 30px;
	}

	.modal-feature {
		margin-top: 30px;
		display: flex;
		// justify-content: flex-start;
		align-items: center;

		& img {
			max-width: 48px;
		}

		&-paragraph {
			@include MullerLight;
			font-size: 16px;
			line-height: 22px;
			letter-spacing: 0.01em;
			color: #0f0f0f;
			margin-left: 21px;
		}
	}
}
