.contacts {
	position: relative;

	&__content {
		padding: 5rem 0 4.93rem 0;	
		background-color: $whiteGray;		
	}

	&__inner {
		max-width: 832px;
		margin: 0 auto;

		& > .row {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
	}

	&__phone-number {
		& strong {
			font-size: 17px;
		}
	}

	&__description {
		z-index: 10;

		&-title {
			@include MullerLight;
			font-size: 13px;
			line-height: 22px;
			color: #000;
			margin-bottom: 5px;
		}

		&-text {
			margin-top: 78px;
			display: flex;
			flex-direction: column;
			align-items: flex-start;
		}

		&-work-time {
			margin-top: 25px;
		}
	}

	&__email {
		margin-top: 28px;

		& .mini-desc {
			text-decoration: underline;
		}
	}

}

.form {
	&__description {
		@include MullerBold;
		font-size: 18px;
		line-height: 23px;
		color: #000;
		margin-bottom: 0;
	}

	&__fields {
		margin-top: 31px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: flex-start;

		& .desc-text {
			@include MullerLight;
			color: #000;
		}
	}

	& label {
		margin-bottom: 14px;

		&:not(:first-child) {
			margin-top: 19px;
		}
	}

	&__input {
		width: 320px;
		height: 50px;
	}

	&__text {
		width: 320px;
		height: 80px;
		resize: none;
	}

	&__btn {
		margin-top: 30px;
		padding-top: 7px;
		width: 320px;
		height: 65px;
		background-color: $red;
		border-radius: 5px;
		border: none;
		color: #fff;
		transition-property: background-color;
		transition-duration: 0.8s;

		&:hover {
			background-color: #b81414;
		}
	}

	&__desc {
		margin: 17px auto 0 auto;
		text-align: center;
		font-size: 13px;
	}
}

.white-bg {
	width: 50%;
	height: 100%;
	background-color: #fff;
	position: absolute;
	top: 0;
	right: 0;
}


/*	 media queries	*/
@media (max-width: $lg) {
	.contacts {
		&__content {
			background-color: #fff;
		}
	
		&__inner {
			max-width: 700px;

			& > .row {
				align-items: flex-start;
			}
		}

		&__phone-number {
				& strong {
					font-size: 16px;
				}
			}

		&__description {
			&-text {
				margin-top: 50px;
			}

			&-work-time {
				& p:not(:first-child) {
					margin-top: 10px;
				}
			}
		}
	}

	.white-bg {
		display: none; 
	}
}

@media (max-width: $md) {
	.contacts {
		&__form {
			margin: 0 auto;
			display: flex;
			flex-direction: column;
			align-items: center;
		}

		&__description {
			margin: 40px auto 0 auto;
		}

		&__phone-number {
			margin-right: auto;
			margin-left: auto;
			text-align: center;
		}

		&__email {
			margin-right: auto;
			margin-left: auto;
			text-align: center;
		}

		&__description {
			&-title {
				text-align: center;
			}

			&-work-time {
				margin-right: auto;
				margin-left: auto;
			}
		}
	}

	.form {
		&__description {
			text-align: center;
		}

		&__desc {
			text-align: left;
			line-height: 5px;
		}

		&__fields {
			margin-right: auto;
			margin-left: auto;
			align-items: center;
			padding-right: 0;
		}
	}
}

@media (max-width: $sm) {
	.contacts {
		&__description {
			margin-top: 50px;
			&-text {
				margin-top: 20px;
			}

			&-work-time {
				max-width: 360px;
				text-align: center;
			}
		}
	}
}


@media (max-width: 425px) {
	.contacts {
		&__description {
			&-title {
				width: 100%;
			}
		}

		&__phone-number {
			& strong {
				font-size: 15px;
			}
		}
	}
}

@media (max-width: 375px) {
	.contacts {
		&__description {
			margin-top: 40px;
		}

		&__phone-number {
			& strong {
				font-size: 14px;
			}
		}

		&__email {
			margin-top: 22px;
		}
	}

	.form {
		&__description {
			font-size: 16px;
		}

		&-field {
			max-width: 280px;
		}

		& label {
			margin-bottom: 12px;

			&:not(:first-child) {
				margin-top: 25px;
			}
		}

		&__btn {
			width: 280px;
		}

		&__desc {
			font-size: 12px;
			text-align: center;
   		line-height: 14px;
		}
	}
}