.partners {
	&__content {
		padding: 7.14rem 4.93rem;
	}

	& .container {
		max-width: 1120px;
	}

	&__title {
		margin: 0 auto;
	}

	&__slider {
		margin-top: 70px;
	}

	&__slider {
		margin-top: 70px;

		&-slide {
			max-width: 220px;
			min-height: 140px;
			max-height: 170px;
			position: relative;

			& img {
				width: 100%;
				height: 100%;
				position: absolute;
				z-index: 1;
			}

			&-wrap {
				max-width: 220px;
				min-height: 170px;
				padding: 22px 17px;
				position: relative;
				background-color: #26b83a;
				box-shadow: 0px 10px 10px 0px rgba(37,183,57,0.2);
			}

			&-description {
				position: absolute;
				z-index: 100;
				@include MullerLight;
				font-size: 12px;
				letter-spacing: 0.05em;
				color: #fff;
			}
		}
	}
}

@media (max-width: $md) {
	.partners {
		& .swiper-container {
			width: 80%;
		}
	}
}


@media (max-width: 620px) {
	.partners {
		& .swiper-container {
			width: 75%;
		}
	}
}

@media (max-width: 480px) {
	.partners {
		& .swiper-container {
			width: 80%;
		}
	}
}

@media (max-width: 460px) {
	.partners {
		& .swiper-container {
			width: 60%;
		}

		&__slider {
			&-slide {
				width: 180px;
				height: 130px;
			}
		}
	}
}