.slider {
	max-width: 1120px;
	width: 100%;
	position: relative;


	&__container {
		width: 84%;
		display: flex;
		justify-self: space-between;
		align-items: center;
	}

	&__slide {
		position: relative;

		& iframe {
			width: 100%;
			max-height: 100%;
			position: absolute;
			top: 0;
			left: 0;
		}

		&-cover {
			max-height: 210px;
			position: absolute;
			z-index: 100;
			top: 0;
			left: 0;
			background-color: #313131;

			& img {
				max-width: 100%;
				max-height: 100%;
			}

			& a {
				display: inline-block;
				position: absolute;
				bottom: 20px;
				left: 20px;
			}
		}
	}

}

/* navigation */
.arrow {
	position: absolute;
	z-index: 1000;
	display: inline-block;
	width: 24px;
	height: 2px;
	background-color: #ccc;
	position: relative;
	transition: all 0.5s ease 0s;
  
   &:before, &:after {
		content: '';
		display: inline-block;
		width: 10px;
		height: 2px;
		background-color: #ccc;
		position: absolute;
		top: 0;
		left: -1px;
		transition: all 0.5s ease 0s;
  	}
  
	&:hover {
		&, &:before, &:after {
			background-color: #e42626;
		}
	}
  
  	&:before {
   	transform: translateY(3px) rotate(40deg);
  	}
  
  	&:after {
   	transform: translateY(-3px) rotate(-40deg);
  	}
}

/* customize */

.swiper-button-prev:after, .swiper-button-next:after {
	content: '';
	width: 0;
	height: 0;
	background-color: #fff;
} 

.swiper-button-prev:focus, .swiper-button-next:focus {
	outline: none;
}

.swiper-button-next .arrow {
	transform: rotate(180deg);
}


/* media queries */
@media (max-width: 1200px) {
	.slider {
		width: 90%;

		&__slide {
			&-cover {
				max-height: 180px;
			}
		}
	}
}

@media (max-width: $lg) {
	.slide {
		&__slide {
			&-cover {
				& a {
					bottom: 10px;
					left: 10px;
				}
			}
		}
	}
}

@media (max-width: $sm) {
	.arrow, .arrow:before, .arrow:after {
		display: none;
	}
}