.subscription {
	background-image: url('../img/bg-images/main_bg.png'); 
	background-position: center center;
	background-size: cover; 
	background-repeat: no-repeat; 

	&__content {
		padding: 19.5rem 4rem 12rem 0;

		& > .container > .row {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
		}
	}

	&__title {
		margin: 0;
		@include ProximaBold;
		font-size: 40px;
		color: #fff;
		text-transform: uppercase;
	}

	&__subtitle {
		@include ProximaBold;
		font-size: 32px;
		line-height: 50px;
		color: #fff;
		margin: 0;
	}

	&__description {
		max-width: 376px;
		margin-top: 30px;
		font-size: 24px;
		line-height: 32px;
		color: #fff;

		&-paragraph {
			@include ProximaLight;
		}

		&-slogan {
			@include ProximaBold;
		}
	}

	&__btn {
		margin-top: 66px;

		& > a {
			padding: 25px 49px 25px 47px;
			background-color: #0f0f0f;
			background-color: $red;
			border-radius: 5px;
			border: none;
			text-decoration: none;
			transition-duration: 0.4s;


			@include ProximaBold;
			font-size: 18px;
			line-height: 26px;
			color: #fff;

			&:hover {
				background-color: #c10b0b;
				box-shadow: 2px -3px $red;
			}
		}
	}
}

/*	Media queries */
@media (max-width: $lg) {
	.subscription {
		&__content {
			& > .container > .row {
				margin-left: 30px;
			}
		}

		&__title {
			font-size: 34px;
		}

		&__subtitle {
			font-size: 26px;
		}

		&__description {
			font-size: 22px;
			margin-top: 24px;
		}

		&__btn {
			& > a {
				&:hover {
					box-shadow: none;
				}
			}
		}
	}
}


/*	 media queries	*/
@media (max-width: $md) {
	.subscription {
		&__content {
			& > .container > .row {
				margin: 0 auto;
			}
		}

		&__title {
			font-size: 28px;
		}

		&__subtitle {
			font-size: 24px;
			line-height: 38px;
		}
	}
	
}


@media (max-width: $sm) {
	.subscription {
		&__title {
			font-size: 24px;
		}

		&__subtitle {
			font-size: 20px;
			line-height: 34px;
		}

		&__btn {
			margin-top: 66px;

			& > a {
				padding: 20px 24px;
			}
		}
	}
}

@media (max-width: $sm) {
	.subscription {
		&__title {
			font-size: 24px;
		}

		&__subtitle {
			font-size: 20px;
			line-height: 34px;
		}

		&__btn {
			margin-top: 66px;

			& > a {
				padding: 20px 24px;
			}
		}
	}
}

@media (max-width: 425px) {
	.subscription {
		&__content {
			padding-right: 0;
		}

		&__subtitle {
			font-size: 18px;
			line-height: 30px;
			display: inline-block;
		}

		&__btn {
			min-width: 300px;
			& > a {
				font-size: 16px;
				padding: 20px;
			}
		}
	}
}

@media (max-width: 375px) {
	.subscription {
		&__title {
			font-size: 22px;
		}

		&__subtitle {
			font-size: 16px;
			line-height: 26px;
		}
	}
}