@import '_burger';

.header {
	&__row {
		margin-top: 30px;
		display: flex;
		justify-content: space-between;
		align-items: center;

		&-phone-number {
			font-size: 18px;
			transition-duration: 0.5s;

			&:hover {
				text-decoration: none;
				color: $red;
			}
		}

		&-description {
			text-align: right;
			margin: 0;
			padding: 6px 0 0 0;
		}

		&-apps {
			display: flex;
			justify-content: space-between;
			max-width: 104px;
		}

		&-app-android {
			padding: 8px 11px 9px 10px;
			margin-right: 10px;
		}

		&-app-ios {
			padding: 6px 13px 11px 10px;
		}
	}
}

.navbar {
	display: flex;
	justify-content: space-between;
	background-color: #fff;
	padding: 0;
	margin-top: 10px;
	padding-right: 15px;
	padding-left: 15px;
	
	&__phone-number {
		font-size: 14px;
		padding: 0;
	}

	&__list {
		display: flex;
		justify-content: space-between;
		margin: 0;
		&-item {
			list-style-type: none;

			&-link {
				color: #000;
				text-decoration: none;
			}
		}
	}

	&__btn {
		max-width: 140px;
		background-color: #c91111;
		padding: 4px 14px;
		letter-spacing: 0.01em;
		font-size: 12px;;
		color: #fff;
		text-decoration: none;
		text-align: center;
		border-radius: 5px;

		&:hover {
			color: #fff;
			text-decoration: none;
			background-color: $red;
		}
	}
} 

.fixed-nav {
	padding: 0 200px;
	height: 50px;
}


/*	Media queries */
@media (max-width: 1326px) {
	.fixed-nav {
		padding: 0 160px;
	}
}


@media (max-width: $xl) {
	.fixed-nav {
		padding: 0 100px;
	}
}


@media (max-width: $lg) {
	.header {
		&__row {
			margin-top: 30px;

			&-description {
				text-align: right
			}

			&-phone-number {
				text-align: center;
			}
		}
	}

	.navbar {
		justify-content: space-around;
		&__list {
			display: none;
			flex-direction: column;
			justify-content: center;
			text-align: center;
			position: fixed;
			min-height: 100vh;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background-color: rgba(0, 0, 0, 0.7);
			z-index: 100;

			&-item {
				@include ProximaBold;
				font-size: 22px;
				margin-top: 20px;

				&-link {
					color: #fff;
					text-decoration: none;
				}
			}
		}

		&__btn {
			order: -1;
		}
	}

	.fixed-nav {
		padding: 0 30px;
	}

	.menu-btn {
		display: flex;
	}
}


@media (max-width: 767px) {
	.header {
		&__row {
			justify-content: space-around;
			flex-wrap: wrap;
			&-phone-number {
				order: 2;
				text-align: center;
				margin-top: 15px;
				margin-left: 50px;
			}

			&-description {
				order: 1;
				text-align: right;
				margin-top: 15px;
			}
		}
	}

	.navbar {
		&__btn {
			padding: 10px 14px;
		}
	}
}

@media (max-width: $sm) {
	.header {
		&__row {
			&-phone-number {
				margin-right: auto;
				margin-left: auto;
				text-align: center;
			}

			&-description {
				text-align: center;
			}
		}
	}
}

@media (max-width: 425px) {
	.header {
		&__row {
			&-phone-number {
				margin-right: auto;
				margin-left: auto;
				text-align: center;
			}

			&-description {
				text-align: center;
			}
		}
	}

	.navbar {
		justify-content: space-between;
		&__phone-number {
			display: none;
		}
	}
}

@media (max-width: 375px) {
	.header {
		&__row {
			&-apps {
				margin-right: 30px;
    			margin-top: 20px;
			}
		}
	}
}