.services {
	&__content {
		padding: 7.14rem 0 7.2rem 0;
	}

	&__inner {
		max-width: 866px;
		margin: 0 auto;

		& .row {
			max-width: 100%;
			margin: 0;
		}
	}

	&__title {
		margin: 0 auto;
	}

	&__items {
		margin-top: -12px;
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;

		& .row {
			min-width: 100%;

			&:not(:first-child) {
				margin-top: 90px;
			}
		}
	}

	&__item {
		display: flex;
		justify-content: start;
		align-items: center;
		transition-duration: 0.8s;
		flex: 0 1 30%;
		margin-top: 92px;

		&:hover {
			color: #e61010;
		}

		&:nth-child(3n+2) {
			padding-left: 15px;
		} 

		&:nth-child(3n) {
			padding-left: 23px;
		}

		&-description {
			max-width: 184px;
			margin-left: 30px;
			display: flex;
			flex-direction: column;

			& p {
				margin: 0;
			}
		}


	}
}

/*	 media queries	*/
@media (max-width: $lg) {
	.services {
		&__inner {
			max-width: 700px;
		}

		&__item {
			&-description {
				margin-left: 15px;
			}
		}
	}
}

@media (max-width: $md) {
	.services {
		&__inner {
			max-width: 500px;
		}

		&__item {
			flex: 1 1 40%;
			padding-left: 25px;
			margin-top: 80px;

			&:nth-child(3n+2) {
				padding-left: 25px;
			} 

			&:nth-child(3n) {
				padding-left: 25px;
			}
		}
	}
}

@media (max-width: $sm) {
	.services {
		&__inner {
			max-width: 360px;
		}

		&__item {
			margin-top: 54px;
			padding-left: 0;

			&:nth-child(3n+2) {
				padding-left: 0;
			} 

			&:nth-child(3n) {
				padding-left: 0;
			}
		}
	}
}

@media (max-width: 425px) {
	.services {
		&__item {
			margin-top: 46px;
		}
	}
}

@media (max-width: 375px) {
	.services {

		&__item {
			flex: 0 0 60%;
			margin-right: auto;
			margin-left: auto;

			&:not(:first-child) {
				margin-top: 36px;
			}
		}
	}
}