.advantages {
	&__content {
		padding: 7.14rem 0;
	}

	&__inner {
		max-width: 934px;
	}

	&__title h2 {
		line-height: 1.2;
	}

	&__table {
		border-collapse: collapse;
		text-align: center;
		display: flex;
		flex-direction: column;
		flex-wrap: nowrap;
	}

	&__row {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
	}

	&__block {
		margin-top: 80px;
	}

	&__page {

		&-title {
			font-size: 16px;
			line-height: 1.2;
			@include ProximaBold;
			letter-spacing: 0.01em;
			color: $dark;
			margin: 0;
		}

		&-items {
			margin-top: 20px;
			margin-bottom: 30px;
		}

		&-item {
			&-text {
			margin-left: 21px;
			display: inline-block;
			font-size: 16px;
			line-height: 35px;
			letter-spacing: 0.01em;
			color: $dark;
			}
		}

		&-btn {
			width: 300px; 
			background-color: $red;
			border-radius: 5px;
			padding: 26px 80px;

			@include ProximaBold;
			font-size: 18px;
			line-height: 40px;
			letter-spacing: 0.01em;
			color: #fff;
			text-align: center;

			&:hover {
				background-color: #c91111;
				text-decoration: none;
				color: #fff;
			}
		}
	}

}

.table {
	@include ProximaLight;
	font-size: 13px;
	line-height: 45px;
	color: #000;
	letter-spacing: 0.01em;
	border-collapse: collapse;
	border: none;

	&__title {
		line-height: 40px;
		font-size: 14px;
	}

	& tr td, & tr th {
		padding-top: 0;
		padding-bottom: 0;
	}

	& th, & td {
		border-top: none;
	}

	& tr th:nth-child(2), & tr td:nth-child(2) {
		background-color: #26b83a;
		color: #fff;
	}

	& tr td:nth-child(2) {
		padding: 0 45px;
	}

	& tr th {
		padding-top: 30px;
	}

	& tr:last-child td {
		padding-bottom: 36px;
	}

	& tr td:first-child {
		text-align: right;
		padding-right: 20px;
		padding-left: 0;
	}

	& tr th:last-child, & tr td:last-child {
		border-right: 1px solid #e8e8e8;
	}

	& tr th:last-child {
		border-top: 1px solid #e8e8e8;
	}

	& tr:last-child td:last-child {
		border-bottom: 1px solid #e8e8e8;
	}
}


/*	 media queries	*/
@media (max-width: 1440px) {
	.advantages {
		&__block {
			margin-top: 70px;
			flex-wrap: wrap;
		}

		&__row {
			padding-left: 0;
		}

		&__page {
			padding: 0;
		}
	}
}

@media (max-width: $xl) {
	.advantages {
		&__block {
			margin-top: 64px;
		}
	}
}

@media (max-width: $lg) {
	.advantages {
		&__row {
			justify-content: center;
		}

		&__block {
			margin-top: 50px;
		}

		&__page {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			align-items: center;
			margin: 40px auto;

			&-btn {
				padding: 16px 60px;
			}
		}

		&__table {
			margin-right: auto;
			margin-left: auto;
		}
	}

	.table {
		line-height: 32px;
		&__title {
			line-height: 32px;
		}

		& tr td:first-child {
			padding-right: 10px;
		}

		& tr td:nth-child(2) {
			padding: 0 20px;
		}
	}

	th {
		width: 200px;
	}
}


@media (max-width: $md) {
	.advantages {
		&__page {
			&-btn {
				width: 260px;
				padding: 12px 30px;;
			}
		}
	}

	.table {
		line-height: 24px;
		&__title {
			line-height: 18px;
			padding: 20px 10px;
		}

		& tr td {
			padding-top: 20px;
		}

		& tr td:nth-child(2) {
			padding-top: 20px;
		}
	}
}

/* #28A745 */
@media (max-width: $sm) {
	.advantages {
		&__inner {
			max-width: 400px;
		}

		&__row {
			padding: 0 10px;
		}
	}

	.table {
		line-height: 18px;
		&__title {
			line-height: 18px;
			padding: 20px 10px;
		}

		& tr td {
			padding-top: 12px;
		}

		& tr td:nth-child(2) {
			padding-top: 12px;
		}
	}
}

@media (max-width: 425px) {
	.table {
		padding: 0;
		line-height: 16px;

		&__title {
			line-height: 14px;
		}
	}
}

@media (max-width: 375px) {
	.table {
		& tr td:first-child {
			padding-right: 5px;
		}

		& tr th:nth-child(2), & tr td:nth-child(2) {
			padding-right: 5px;
			padding-left: 5px;
		}
	}
}