/*		ProximaNova	*/
@mixin ProximaLight { 
	font-family: 'Proxima Nova';
	font-weight: 300;
	font-style: normal;
}

@mixin ProximaRegular { 
	font-family: 'Proxima Nova';
	font-weight: normal;
	font-style: normal;
}

@mixin ProximaBold { 
	font-family: 'Proxima Nova';
	font-weight: 700;
	font-style: normal;
}


/*		Muller	*/
@mixin MullerLight {
	font-family: 'Muller';
	font-weight: 300;
	font-style: normal;
}

@mixin MullerRegular {
	font-family: 'Muller';
	font-weight: normal;
	font-style: normal;
}

@mixin MullerMedium { 
	font-family: 'Muller';
	font-weight: 500;
	font-style: normal;
}

@mixin MullerBold { 
	font-family: 'Muller';
	font-weight: 700;
	font-style: normal;
}