.free-consultation {
	&__content {
		padding: 7.14rem 0;
	}

	&__inner {
		max-width: 890px;
		margin: 0 auto;
	}

	&__bg {
		min-height: 470px;
	}

	&__description {
		display: flex;
		flex-direction: column;
		align-items: start;
	}

	&__title {
		& > h2 {
			@include MullerBold;
			font-size: 30px;
			line-height: 1.2;
			letter-spacing: 0.05em;
			color: #fff;
		}
	}

	&__description-list {
		margin-top: 5.57rem;
	}

	&__list {
		font-size: 18px;
		padding: 0;
		&-item {
			@include MullerMedium;
			line-height: 1.2;
			color: #e41e1e;

			&-text {
				@include MullerLight;
				line-height: 1.2;
				color: #fff;
				margin-left: 20px;

				& > a {
					text-decoration: underline;
					color: #fff;
				}
			}
		}
	}

	&__link {
		&:not(:first-child) {
			display: inline-block;
			margin-top: 30px;
		}
	}
}

/*  media queries  */
@media (max-width: $xl) {
	.free-consultation {
		&__title {
			& > h2 {
				font-size: 28px;
			}
		}
	}
}


@media (max-width: $lg) {
	.free-consultation {
		&__title {
			text-align: center;

			& > h2 {
				font-size: 26px;
			}
		}

		&__description {
			align-items: center
		}

		&__links {
			display: flex;
			justify-content: center;
			align-items: center;
			margin-top: 30px;
		}

		&__link {
			&:not(:first-child) {
				margin-top: 0;
				margin-left: 25px;
			}
		}
	}
}


@media (max-width: $md) {
	.free-consultation {
		&__title {
			& > h2 {
				font-size: 24px;
			}
		}

		&__description-list {
			margin-left: 30px;
		}

		&__list {
			font-size: 16px;
		}
	
		&__link {
			& img {
				max-width: 90%;
			}
		}
	}
}


@media (max-width: $sm) {
	.free-consultation {
		&__title {
			& > h2 {
				font-size: 22px;
			}
		}

		&__list {
			&-item {
				&-text {
					margin-left: 5px;
				}
			}
		}

		&__link {
			display: flex;
			justify-content: center;

			& img {
				max-width: 80%;
			}
		}
	}
}

@media (max-width: 425px) {
	.free-consultation {
		&__title {
			& > h2 {
				font-size: 20px;
			}
		}

		&__list {
			font-size: 14px;

			&-item {
				&:not(:first-child) {
					margin-top: 15px;
				}
			}
		}
	}
}